import React from 'react';
import ReactDOM from 'react-dom';
import './index.css' ;

const l = 'chandan'
const currndate = new Date().toLocaleTimeString();
const currn  = new Date().getHours();
let g = 'hello'
const colorchange = {}
const colordate = {}
colordate.color = 'cyan'

if(currn>1 && currn<12){
  g = 'GOOD MORNING'
  colorchange.color  = "green"
}
else if(currn>12 && currn<19){
  g = 'Good Evening'
  colorchange.color  = "red"
}
else{
  g = 'GOOD NIGHT'
  colorchange.color  = "brown"
}

ReactDOM.render(
  <>
  <div className="bordercolor">
  <div className="borderinner" >
  <h1 className="title">
    HELLO MASTER  <h2 style={colorchange}> {g}</h2>
    <h3 style={colordate}> {currndate}</h3>
  </h1>
  </div>
  </div>
  

  <div className="imageGrid">
  <img src = "https://picsum.photos/200/300" />
  <img src = "https://picsum.photos/205/300" />
  <img src = "https://picsum.photos/210/300" />
  </div>
  
  </> ,
  document.getElementById('root')
)
